export default {
	data() {
		return {
			value: '',
			list: [],
			active: false,
			activeIndex: 0,
			text: '全部',
			textShow: false,
		}
	},
	computed:{
		listData() {
			let list = JSON.parse(JSON.stringify(this.list))
			if (this.activeIndex === 0) {
				this.text = "全部"
				return list
			}
			if (this.activeIndex === 1) {
				this.text = "待办"
				list = list.filter(item => !item.checked)
				return list
			}
			if (this.activeIndex === 2) {
				this.text = "已完成"
				list = list.filter(item => item.checked)
				return list
			}
		}
	},
	methods: {
		create() {
			if(this.active) {
				this.close()
			} else {
				this.open()
			}
			// 因为v-if，所以需要元素显示以后才可以，所以需要添加一个延迟是动画效果有效
		},
		open() {
			this.active = true
			this.$nextTick(function(){
				setTimeout(() => {
					this.textShow = true
				}, 50)
			})
		},
		close() {
			this.textShow = false
			this.$nextTick(function(){
				setTimeout(() => {
					this.active = false
				}, 350)
			})
		},
		add() {
			if (this.value === '') {
				uni.showToast({
					title: "请输入内容",
					icon: "none",
					checked: false,
				})
			} else {
				let todoItem = {
					id: 'id' + new Date().getTime(),
					content: this.value,
				}
				this.list = [todoItem, ...this.list]
			}
			this.value = ''
			// this.active = false
			// this.textShow = false
			this.close()
		},
		finish(id) {
			let index = this.list.findIndex(item => item.id === id)
			this.list[index].checked = !this.list[index].checked
			let newItem = this.list[index]
			this.list.splice(index, 1, newItem)
		},
		tab(index) {
			this.activeIndex = index
		}
	}
}
